.about_section_inner {
  width: 90%;
  margin: auto;
  max-width: 1247px;
  display: flex;
  padding: 2% 0;
}
.about_heading {
  width: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.about_heading > h1 {
  font-size: calc(2.7rem);
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
  padding: 1rem;
  border-radius: 5px;
}
.about_description {
  width: 68%;
  display: flex;
  flex-direction: column;
  line-height: 18px;
}
.about_desc_desc {
  text-align: justify;
  font-size: calc(1rem);
}
.about_desc_icons {
  display: flex;
  align-items: center;
  gap: 2%;
  font-size: 1.2rem;
  margin-top: 1rem;
}
#resume-link-2 {
  border: none;
  color: #000000;
  background-color: #00ff0000;
  display: flex;
  align-items: center;
  padding: 0.3rem 0.5rem;
  gap: 5px;
  cursor: pointer;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
  animation: 0.3s ease-in-out;
}
#resume-link-2:focus {
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
  animation: 0.3s ease-in-out;
}
@media screen and (max-width: 1024px) and (min-width: 481px) {
  .about_section_inner {
    flex-direction: column;
  }
  .about_heading {
    width: 100%;
    margin-bottom: -2em;
  }
  .about_description {
    width: 100%;
    margin-bottom: 1rem;
  }
}

@media screen and (max-width: 481px) {
  .about_section_inner {
    flex-direction: column;
  }
  .about_heading {
    width: 100%;
    margin-top: -0.5em;
    margin-bottom: -2em;
  }
  .about_description {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-bottom: 1rem;
  }
}
